import { navigate } from "gatsby";
import { useLocation } from "@reach/router";
import { GatsbyImage, getImage } from "gatsby-plugin-image";

import { Wrapper, ContentTitle } from "style/content/malog/style.js";
import * as s from "style/content/malog/Recent.js";
const Recent = ({ results }) => {
  const location = useLocation();

  console.log(results);

  return (
    <Wrapper>
      {location.pathname.startsWith("/categories") ? null : (
        <ContentTitle>
          Recent
          <br />
          Posts
        </ContentTitle>
      )}
      <s.RecentWrapper>
        {results &&
          results.map((result) => (
            <s.RecentCard key={result.id} onClick={() => navigate(`/post${result.slug}`)}>
              {typeof result.thumbnail === "string" ? null : (
                <GatsbyImage image={getImage(result.thumbnail?.childImageSharp?.gatsbyImageData)} alt="thumbnail" loading="eager" />
              )}
              <s.CardTitle>{result.title}</s.CardTitle>
              <s.CardDescription>{result.description}</s.CardDescription>
              <s.Categories>{result.categories && result.categories.map((category) => <span key={category}>{category}</span>)}</s.Categories>
            </s.RecentCard>
          ))}
      </s.RecentWrapper>
    </Wrapper>
  );
};

export default Recent;
